*{
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
    align-items: center;
    display: flex;
    justify-content: center;
    min-height: 100vh;
}

.title {
    background: #ccc;
    background: linear-gradient(90deg, #ad5389, #3c1053);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 4rem; 

    -webkit-text-fill-color: transparent;
    background-clip: text;

    margin-top: 0;
    text-align: center;

}

.wrapper {
    width: 100%;
    padding: 10px;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
}

.wrapper .wrapper-input {
    height: 40px;
    width: 100%;
    border: none;
    position: relative;
}

.wrapper .wrapper-input input{
    width: 100%;
    height: 100%;
    border: none;
    color:gray;
    border: none;
    outline: none;
    font-size: 20px;
}

.wrapper .wrapper-input .lbl-name {
    position: absolute;
    bottom: 0px;
    left: 0;
    color: grey;
    pointer-events: none;
    border: none;
    font-size: 30px;
    transition: all 0.3s ease;
}

.wrapper .wrapper-input input:focus ~ label,
.wrapper .wrapper-input input:valid ~ label{
    transform: translateY(-30px);
    font-size: 1rem;
    margin-top: 50%;
    border: none;
    color: #4158d0;
}

.wrapper .wrapper-input .underline {
    position: absolute;
    bottom: 0px;
    height: 2px;
    width: 100%;
}

.wrapper .wrapper-input .underline:before {
    position: absolute;
    content: " ";
    height: 100%;
    width: 100%;
    background: #4158d0;
    transform: scaleX(0);
    transition: transform 0.3s ease;
}

.wrapper .wrapper-input input:focus ~ .underline:before,
.wrapper .wrapper-input input:valid ~ .underline:before {
    transform: scaleX(1);
}

.card {
    align-content: center;
    border: 0.1px;
    border-radius: 4px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.1);  
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    width: 400px;
    height: 300px;
    margin: 10px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}